*
{
    margin: 0;
    padding: 0;
}

.wrapmain
{
    width: 100%;
    height: 100vh;
    background-color: aqua;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.main
{
    width: 25%;
    height: 80vh;
    background-color:white;
    border-radius: 15px;
    box-shadow: 5px 5px 25px -2px black;
}

.heading
{
    color: white;
    background: transparent;
    background-color: blueviolet;
    padding: 6px 0 2px 0;
    margin:30px 0;
    box-shadow: 5px 5px 15px -5px black;
}

input
{
    text-align: center;
    height: 30px;
    top: 10px;
    border: none;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    width: 60%;
    border-bottom: 2px solid purple;
    outline: none;
}

.addition
{
    min-height: 40px;
    width: 40px;
    border-radius: 50%;
    border-color: transparent;
    background-color:green;
    color: white;
    font-size: 40px;
    border: none;
    outline: none;
    margin-left: 10px;
    box-shadow: 5px 5px 15px -5px black;
}

button:hover
{
    background-color:purple;
}

ol
{
    margin-top: 30px;
}
ol li
{
padding-left: 0px;
text-align: left;
font-size: 20px;
font-weight: 500;
min-height: 40px;
display: flex;
align-items: center;
color: gray;
text-transform:capitalize;
}

.listview .fa-car
{
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: palevioletred;
    border-radius: 50%;
}